import axios from "axios";

//fetching
const getPatientData = async () => {
	const res = await axios.get(`/patient/`).catch((err) => console.log(err));
	//${id}
	const data = await res.data;
	return data;
};
const fethcDetails = async () => {
	const res = await axios.get(`/api/`);
	const data = await res.data;
	console.log(data);
	return data;
};
//put
const postData = async (input) => {
	const res = await axios
		.put(`/`, {
			title: input.title,
			desc: input.desc,
			image: input.image,
		})
		.catch((err) => console.log(err));
	const data = await res.data;
	return data;
};
//delete
const deleteRequest = async () => {
	const res = await axios.delete(`/`).catch((err) => console.log(err));
	const data = await res.data;
	return data;
};

//patient page
const postPatientChain = async (chain) => {
	const res = await axios
		.post(`/blockchain/putgenesis`, {
			block_no: chain.block_no,
			timestamp: chain.timestamp,
			data: chain.data,
			precedingHash: chain.precedingHash,
			nonce: chain.nonce,
			hash: chain.hash,
		})
		.catch((err) => console.log(err));
	const data = await res.data;
	return data;
};
const getPatientChain = async () => {
	const res = await axios
		.get(`/blockchain/getblockchain`)
		.catch((err) => console.log(err));
	//${id}
	const data = await res.data;
	return data;
};

export {
	postPatientChain,
	getPatientChain,
	deleteRequest,
	postData,
	fethcDetails,
	getPatientData,
};
