import React from "react";
import { Grid, Typography } from "@mui/material";
import Logo from "../Assets/logo.svg";
import { Link } from "react-router-dom";

export default function Navbar() {
	return (
		<Grid
			item
			container
			className="navBarContainer"
			justifyContent="space-between"
		>
			{/* logo n title */}
			<Grid item container md={3} alignItems="center">
				<Grid item sx={{ display: "flex", alignItems: "center" }}>
					<img src={Logo} alt="logo" className="imgLogo" />
				</Grid>
				<Grid item>
					<Typography variant="body1" className="logoTitle">
						Medico
					</Typography>
				</Grid>
			</Grid>
			{/* other link options */}
			<Grid
				item
				container
				md={7}
				justifyContent="space-evenly"
				alignItems="center"
			>
				<Grid item>
					<Link to="/">
						<Typography className="links" variant="body2">
							Home
						</Typography>
					</Link>
				</Grid>
				<Grid item>
					<Link to="/patient">
						<Typography className="links" variant="body2">
							Patients
						</Typography>
					</Link>
				</Grid>
				<Grid item>
					<Link to="/doctor">
						<Typography className="links" variant="body2">
							Doctors
						</Typography>
					</Link>
				</Grid>
				<Grid item>
					<Link to="/insurance">
						<Typography className="links" variant="body2">
							Insurer
						</Typography>
					</Link>
				</Grid>
				<Grid item>
					<Link to="/login">
						<Typography className="links" variant="body2">
							Login
						</Typography>
					</Link>
				</Grid>
			</Grid>
		</Grid>
	);
}
